// //////////////////////////////
// @@ FOOTER REGION
// //////////////////////////////

.r-footer {
  background-color: map-get($colors, accent-two);

  &__inner {
    padding: $default-padding $double-padding;
    display: flex;
    flex-direction: column;

    @include mq(tablet) {
      padding: $default-padding 0;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;

      & > div {
        width: 43%;
      }
    }

    @include mq (desktop) {
      & > div {
        width: 22%;
      }
    }
  }


  &__brochure {
    margin-bottom: $margin-b;

    &-images {
      max-width: 25rem;
    }
  }

  &__opening-hours {
    margin-bottom: $margin-b;
  }

  &__contact {
    margin-bottom: $margin-b;

    &-text {
      img {
        max-width: 4.6rem;
      }
    }
  }

  &__partners {
    &-icons {
      a {
				display: inline-block;

        &:not(:last-child) {
          margin-bottom: $margin-b;
        }
      }
    }
	}
	
	&__copy {
		display: flex;
    justify-content: center;
    font: {
      size: 1.2rem;
    }

		a {
			color: map-get($colors, black);
		}
	}
}
